.content {
    display: grid;
    grid-area: 1 / 3 / 2 / 4;
    grid-template-columns: 300px 1fr;
    grid-template-rows: auto;
    margin-bottom: 50px;
}
.btnGroup {
    grid-area: 1 / 1 / 2 / 2;
    padding-left: 10px;
    text-align: center;
}

.headerPara {
    font-family: 'Cinzel', serif;
    font-size: 24px;
    text-align: center;
}

.buttonHead {
    font-family: 'Cinzel', serif;
    font-size: 24px;
    margin: 5px 0 5px 0;
}

.button {
    width: 250px;
    height: 50px;
    margin-bottom: 10px;
    border-radius: 5px;
    background-color: #e8dacc;
    font-family: 'Cinzel', serif;
    font-size: 18px;
    cursor: pointer;
}

.button:hover {
    background-color: #fcf2e8;
}

.button:active {
    background-color: white;
}

#btnPara {
    font-family: Avenir, 'sans-serif';
    font-size: 12px;
    margin-bottom: 10px;
}
.slides {
    grid-area: 1 / 2 / auto / 3;
    margin: 5px;
    padding-left: 20px;
    border: 5px;
    border-color: black;
    border-radius: 5px;
    justify-items: center;
}